body, html, div {
  font-family: 'PT Sans Narrow', sans-serif;
  font-size: 20px;
}
body {
  background-color: black;
  overflow-x: hidden;
}

@-webkit-keyframes fadeToBlack {
  from {-webkit-clip-path: circle(1000px at center);clip-path: circle(1000px at center);};
  to {-webkit-clip-path: circle(0px at center);clip-path: circle(0px at center);}
}

@keyframes fadeToBlack {
  from {-webkit-clip-path: circle(1000px at center);clip-path: circle(1000px at center);};
  to {-webkit-clip-path: circle(0px at center);clip-path: circle(0px at center);}
}

@-webkit-keyframes newDay {
  from {opacity: 0};
  to {opacity: 1}
}

@keyframes newDay {
  from {opacity: 0};
  to {opacity: 1}
}

.App {
  display: -ms-flexbox;
  display: flex;
  width: 100vw;
  height: 100vh;
  color: white;
}

.App.selectionPhase {
  -webkit-animation-name: fadeToBlack;
          animation-name: fadeToBlack;
  -webkit-animation-duration: 9s;
          animation-duration: 9s;
  -webkit-animation-timing-function: ease-in;
          animation-timing-function: ease-in;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
  -webkit-clip-path: circle(0px at center);
          clip-path: circle(0px at center);
}

.App.newDay {
  -webkit-clip-path: none;
          clip-path: none;
  opacity: 1;
  -webkit-animation-name: newDay;
          animation-name: newDay;
  -webkit-animation-duration: 3s;
          animation-duration: 3s;
  -webkit-animation-timing-function: ease-in;
          animation-timing-function: ease-in;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
}

.events {
  -ms-flex-preferred-size: 20%;
      flex-basis: 20%;
  background: #222222;
  -ms-flex-negative: 0;
      flex-shrink: 0;
}

.characters {
  background: #000000;
  -ms-flex-positive: 1;
      flex-grow: 1;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  padding: 10px;
}

.character {
  -ms-flex-preferred-size: 200px;
      flex-basis: 200px;
  height: 200px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}

.characterImageContainer {
  margin: 100px 0 0 100px;
  -ms-flex-positive: 1;
      flex-grow: 1;
  position: relative;
}

.characterImage, .characterShield, .characterPancarte, .characterName {
  position: absolute;
  width: 180px;
  height: 180px;
  border-radius: 10000px;
  margin: -100px 0 0 -100px;
}

.characterPancarte {
  height: 190px;
  border-radius: 0;
  background: url('pancarte.png');
  background-size: 100%;
}

.characterImageContainer.rot5 > div {
  -webkit-transform: rotate(4deg);
      -ms-transform: rotate(4deg);
          transform: rotate(4deg);
}

.characterImageContainer.rot10 > div {
  -webkit-transform: rotate(7deg);
      -ms-transform: rotate(7deg);
          transform: rotate(7deg);
}

.characterImageContainer.rot15 > div {
  -webkit-transform: rotate(10deg);
      -ms-transform: rotate(10deg);
          transform: rotate(10deg);
}

.characterImageContainer.rot-5 > div {
  -webkit-transform: rotate(-4deg);
      -ms-transform: rotate(-4deg);
          transform: rotate(-4deg);
}

.characterImageContainer.rot-10 > div {
  -webkit-transform: rotate(-7deg);
      -ms-transform: rotate(-7deg);
          transform: rotate(-7deg);
}

.characterImageContainer.rot-15 > div {
  -webkit-transform: rotate(-10deg);
      -ms-transform: rotate(-10deg);
          transform: rotate(-10deg);
}

.characterImageContainer.flip > video {
  -webkit-transform: scale(-1, 1);
      -ms-transform: scale(-1, 1);
          transform: scale(-1, 1);
}

.characterImage {
  width: 140px;
  height: 140px;
  margin: calc(-100px + 20px) 0 0 calc(-100px + 20px);
}

.characterImageBig {
  border-radius: 1000px;
  margin: 100px 0 0 -603px;
  width: 450px;
  position: absolute;
}

.characterShield {
  background: url('hublot.png');
  background-size: contain;
  background-repeat: no-repeat;
  background-position: 50% 50%;
}

.characterName {
  text-align: center;
  padding-top: 152px;
  height: 0;
}

.characterText {
  background: url('Bulle.png');
  margin: -100px 0 0 -100px;
  width: 200px;
  height: 200px;
  background-size: contain;
  background-repeat: no-repeat;
  background-position: 0px 15px;
  display: none;
  -webkit-transform: rotate(0deg);
      -ms-transform: rotate(0deg);
          transform: rotate(0deg);
}

@-webkit-keyframes rotating {
  from{
    -webkit-transform: rotate(30deg) scale(1.5);
    opacity: 0;
  }
}
@keyframes rotating {
  from{
    -webkit-transform: rotate(30deg) scale(1.5);
            transform: rotate(30deg) scale(1.5);
    opacity: 0;
  }
}

.characterText:not(:empty) {
  display: block;
  -webkit-animation: rotating 0.5s ease-out;
  animation: rotating 0.5s ease-out;
}

.characterTextInner {
  color: black;
  padding: 34px 24px 0px;
}

.selected {
  background: green;
}

.dead {
  background: red;
}

.sacrificeAnnouncement, .factAnnouncement {
  background: black;
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  text-align: center;
  background-image: url('bigPancarte.png');
  background-position: center 180px;
  background-repeat: no-repeat;
}

.sacrificeAnnouncement.visible,
.factAnnouncement.visible {
  display: block;
}

.shamanBigShield {
  background-image: url('hublot.png');
  width: 550px;
  height: 550px;
  background-size: contain;
  display: -ms-inline-flexbox;
  display: inline-flex;
  position: absolute;
  margin: 50px 0 0 -650px;
}

.shamanMessage {
  margin: 310px 0 0 -70px;
  width: 428px;
  font-size: 40px;
  display: -ms-inline-flexbox;
  display: inline-flex;
  position: absolute;
  color: white;
}

.shamanOK {
  margin: 590px 0px 0 120px;
  width: 200px;
  height: 80px;
  display: -ms-inline-flexbox;
  display: inline-flex;
  position: absolute;
  background: url('pancarteButton.png');
  background-repeat: no-repeat;
  background-size: contain;
  color: black;
  padding-left: 80px;
  padding-top: 40px;
  font-size: 34px;
  cursor: pointer;
  font-weight: bold;
}



